import React , {useState }  from 'react';
import Layout from '../containers/common/common-layout'
import axios from 'axios';


const Profile = () => {
const [file,setFile]=useState(null);

const onInputChange = (e)=>{
    setFile(e.target.files[0])
}
const clicked=(e)=>{
    e.preventDefault();

    const formData= new FormData()
    formData.append('username','selket');
    formData.append('photo',file)


}
    return(
    <Layout pathList={['portfolio basic', 'basic-4 grid with title']} pathTitle="Streams">
        <div>hello</div>
        <input type="file" onChange={onInputChange}></input>
        <button onClick={clicked}>update image</button>
        <img src="images/users/aaa"></img>
    </Layout>
)
    }


export default Profile;